import React from 'react'
import { BigScreen, SmallScreen } from '../../../../helpers/mediaQueries'
import {
  Column,
  Container,
  Row
} from '../../../..'
import { FormFooter } from './FormFooter'
import { PayCardFooter } from './PayCardFooter'
import { SocialMediaFooter } from './SocialMediaFooter'
import { getGlobalStyle } from '../../../../helpers'
import { Spacer } from '../../../layout'
import { Site } from '../../../../shared/interfaces/Site'

export interface BottomFooterEntriesData {
  label?: string;
  showNewsletter?: string;
}

export interface BottomFooterData {
  contentType: string;
  entries: BottomFooterEntriesData[];
  label: string;
}

export interface BottomFooterProps {
  data: BottomFooterData;
  site: Site
}
export const BottomFooter = ({ data, site = 'unimarc' }: BottomFooterProps): React.ReactElement => {
  const { showNewsletter } = data.entries[0]
  return (
    <Container
      alignItems='center'
      backgroundColor={getGlobalStyle('--color-background-gray')}
      isWrap
      justifyContent='center'
    >
      <BigScreen>
        <Row
          customWidth='95'
          maxWidth='113px'
          minWidth='1130px'
          tagName='section'
        >
          <Column customWidth='100'>
            {JSON.parse(showNewsletter) && <FormFooter site={site} />}
          </Column>
          <Column customWidth='50'>
            <SocialMediaFooter site={site} />
          </Column>
          <Column customWidth='44'>
            <PayCardFooter />
          </Column>
        </Row>
      </BigScreen>

      <SmallScreen>
        <Row
          alignItems='center'
          customWidth='90'
          isWrap
          justifyContent='center'
          margin='20px'
          tagName='section'
        >
          {JSON.parse(showNewsletter) &&
            <>
              <FormFooter site={site} widthInput='100%' />
              <Spacer.Horizontal size={16} />
            </>
          }

          <SocialMediaFooter site={site} />

          <Spacer.Horizontal size={16} />

          <PayCardFooter />
        </Row>
      </SmallScreen>
      <Spacer.Horizontal size={96} />
    </Container>
  )
}