import { Spacer } from '../../../../../../layout'
import { Link } from '../../../../../../atoms'
import { MiddleFooterEntriesData } from '../../..'
import { getGlobalStyle } from '../../../../../../../helpers'

export const OnlyTitle = ({ title, link }: MiddleFooterEntriesData): React.ReactElement => {

  if(!title) { 
    return null 
  }

  return (
    <>
      <Link 
        color={getGlobalStyle('--color-icons-black')} 
        customWidth='100'
        fontSize='md' 
        href={link}
        underline='none' 
      >
        {title}
      </Link>
      <Spacer.Horizontal size={8} />
    </>
  )
}