import React from 'react'
import { MiddleFooterEntriesData } from '../..'
import { OnlyTitle } from './OnlyTitle'
import { SubTitleWithIcon } from './SubTitleWithIcon'
import { TitleWithSecondTitle } from './TitleWithSecondTitle'

const isOnlyTitle = (entry: MiddleFooterEntriesData): boolean => {
  return typeof entry?.icon == 'undefined'
}

const isTitleWithIcon = (entry: MiddleFooterEntriesData): boolean => {
  return typeof entry?.icon == 'string' && !!entry?.description
}

const isTitleWithSecondTitle = (entry: MiddleFooterEntriesData): boolean => {
  return typeof entry?.secondIcon == 'string' && !!entry?.secondTitle && typeof entry?.description == 'undefined'
}

interface MiddleFooterContentProps {
  entry: MiddleFooterEntriesData
}

export const MiddleFooterContent = ({ entry }: MiddleFooterContentProps): React.ReactElement => {
  return (
    <>
      {isOnlyTitle(entry) && <OnlyTitle link={entry?.link} title={entry?.title} />}
      {isTitleWithSecondTitle(entry) && 
        <TitleWithSecondTitle  
          icon={entry?.icon}
          link={entry?.link}
          secondIcon={entry?.secondIcon}
          secondTitle={entry?.secondTitle}
          subTitle={entry?.subTitle}
          title={entry?.title}
        />
      }
      {isTitleWithIcon(entry) && 
        <SubTitleWithIcon
          description={entry?.description}
          icon={entry?.icon}
          label={entry?.label}
          link={entry?.link}
          subTitle={entry?.subTitle}
        />
      }
    </>
  )
}