import React from 'react'
import { Column, Container, Row } from '../../../layout'
import { BigScreen,SmallScreen } from '../../../../helpers/mediaQueries'
import { MiddleFooterColumns } from './MiddleFooterColumn'
import { Acordeon } from '../../../molecules'
import { Divider } from '../../../atoms'
import { Icons } from '../../../../assets/SvgComponents/SvgIcons'
import { getGlobalStyle } from '../../../../helpers'

export interface MiddleFooterEntriesData {
  description?: string;
  entries?: [];
  icon?: keyof (typeof Icons);
  label?: string;
  link?: string;
  secondIcon?: keyof (typeof Icons);
  secondTitle?: string;
  subTitle?: string;
  title?: string;
}

export interface MiddleFooterData {
  contentType: string;
  entries: MiddleFooterEntriesData[];
  label: string;
}

export interface MiddleFooterProps {
  data: MiddleFooterData[]
}

export const MiddleFooter = ({ data }: MiddleFooterProps): React.ReactElement => {
  return (
    <>
      {
        data?.length > 0 && data.map((item: MiddleFooterData, i:number) => {
          return (
            <React.Fragment key={`${item.contentType}__${i}`}>
              <BigScreen>
                <Container 
                  alignItems='start'
                  justifyContent='center'
                  tagName='article'
                >
                  <Row justifyContent='center' tagName='article'>
                    <MiddleFooterColumns item={item} />
                  </Row>
                </Container>
              </BigScreen>

              <SmallScreen>
                <>
                  <Container backgroundColor={getGlobalStyle('--color-background-gray')} tagName='section' >
                    <Row tagName='article' >
                      <Column
                        alignItems='center'
                        justifyContent='center' 
                        margin='1% 6% 1% 6%'
                        maxWidth='100%'
                      >
                        <Acordeon  
                          alignItemsIcon='end'
                          fontWeightText='semibold' 
                          heightIcon='25px'
                          maxWidthText='220px'
                          minWidthText='220px'
                          positionText='bottom' 
                          titleClose={item.label}
                          titleOpen={item.label} 
                          underline='none'
                        >
                          <MiddleFooterColumns item={item} />
                        </Acordeon>
                      </Column>
                    </Row>
                  </Container>
                  <Container margin='0% 6% 0% 6%' >
                    <Divider verticalMargin={0} />
                  </Container> 
                </>
              </SmallScreen>
            </React.Fragment>
          )
        })
      }
    </>
  )
}