import React from 'react'
import {
  Container,
  Icon,
  Link,
  Row,
  Text
} from '../../../../..'
import { getGlobalStyle } from '../../../../../helpers'
import { Site } from '../../../../../shared/interfaces/Site'
import { Spacer } from '../../../../layout'
import styles from './SocialMediaFooter.module.css'

export const SocialMediaFooter = ({ site = 'unimarc' }: { site: Site }): React.ReactElement => {

  const getURLs = () => {
    if (site === 'alvi') {
      return {
        facebookLink: 'https://es-la.facebook.com/supermercadoalvi/',
        instagramLink: 'https://www.instagram.com/clubalvi/',
        twitterLink: 'https://twitter.com/alvichile?lang=es',
        youtubeLink: '/'
      }
    }

    // UNIMARC
    return {
      facebookLink: 'https://www.facebook.com/unimarc',
      instagramLink: 'https://www.instagram.com/unimarc/',
      twitterLink: 'https://twitter.com/unimarc',
      youtubeLink: 'https://www.youtube.com/user/unimarccl'
    }
  }

  const {
    facebookLink,
    instagramLink,
    twitterLink,
    youtubeLink
  } = getURLs()

  return (
    <Container isWrap tagName='article'>
      <Text fontWeight='semibold'>Síguenos</Text>
      <Spacer.Horizontal size={12} />
      <Row>
        <Link href={facebookLink}>
          <Container className={styles.facebookIcon}>
            <Icon 
              clickable='pointer'
              color={getGlobalStyle('--color-icons-black')}
              name='Facebook'  
              sizes='sm'
            />  
          </Container>
        </Link>
        {
          site === 'unimarc' &&
          <>
            <Spacer.Vertical size={16} />
            <Link href={twitterLink}>
              <Container className={styles.twitterIcon}>
                <Icon
                  clickable='pointer'
                  color={getGlobalStyle('--color-icons-black')}
                  name='Twitter'
                  sizes='sm'
                />
              </Container>
            </Link>
            <Spacer.Vertical size={16} />
            <Link href={youtubeLink}>
              <Container className={styles.youtubeIcon}>
                <Icon 
                  clickable='pointer' 
                  color={getGlobalStyle('--color-icons-black')} 
                  name='Youtube'
                  sizes='sm'
                />
              </Container>
            </Link>
          </>
        }
        <Spacer.Vertical size={16} />
        <Link href={instagramLink}>
          <Container className={styles.instragramIcon}>
            <Icon 
              clickable='pointer'
              color={getGlobalStyle('--color-icons-black')} 
              name='Instagram'
              sizes='sm'
            />
          </Container>
        </Link>
      </Row>
    </Container>
  )
}