import React, { useState } from 'react'
import {
  Button,
  ButtonStatus,
  Checkbox,
  Column,
  Input,
  Link,
  Row,
  Text
} from '../../../../..'
import classNames from 'classnames'
import { Container, Spacer } from '../../../../layout'
import { getGlobalStyle } from '../../../../../helpers'
import { Site } from '../../../../../shared/interfaces/Site'
import styles from './FormFooter.module.css'

type FormFooterProps = {
  widthInput?: string;
  site: Site
}

const SITE_ALVI = 'alvi'

export const FormFooter = ({ widthInput= '50%', site = 'unimarc' }: FormFooterProps): React.ReactElement => {
  const [acceptTerms,setAceptTerms] = useState(false)
  const [statusButton, setStatusButton] = useState<ButtonStatus>('disabled')

  const getSettings = (site: Site) => {
    if (site === SITE_ALVI) {
      return {
        btnBorderRadius: getGlobalStyle('--border-radius-full'),
        checkboxColor: styles.customCheckbox__alvi,
        customStyle: styles.customInput__alvi,
        iconColor: getGlobalStyle('--color-base-white'),
        placeholder: 'Ingresa tu email',
        termsUrl: 'https://www.alvi.cl/static/terminos-condiciones'
      }
    }
    // UNIMARC default settings
    return {
      btnBorderRadius: getGlobalStyle('--border-radius-xs'),
      checkboxColor: styles.customCheckbox__unimarc,
      customStyle: styles.customInput__unimarc,
      iconColor: getGlobalStyle('--color-primary-red'),
      placeholder: 'Nombre@gmail.com',
      termsUrl: '#'
    }
  }

  const {
    customStyle,
    btnBorderRadius,
    iconColor,
    placeholder,
    checkboxColor,
    termsUrl
  } = getSettings(site)

  return (
    <Container isWrap tagName='article'>
      <Text fontWeight='semibold'>Únete a nuestra lista de correos</Text>
      <Spacer.Horizontal size={12} />
      {/* message */}
      <Container isWrap>
        <Text fontSize='md' fontWeight='regular'>Entérate de todas las ofertas que traemos para ti</Text>
        <Spacer.Horizontal size={16} />
      </Container>
      {/* input and button */}
      <Row customClassName={customStyle}>
        <Input
          borderRadius='none'
          height='45px'
          padding='newsletter'
          placeholder={placeholder}
          textAlign='left'
          width={widthInput}
        />
        <Column
          borderRadius='none'
          className={styles.containerBtn}
          width='113px'
        >
          <Button
            borderRadius={btnBorderRadius}
            height='45px'
            label='Enviar'
            minWidth='113px'
            site={site}
            size='large'
            status={statusButton}
          />
        </Column>
      </Row>
      <Spacer.Horizontal size={16} />
      {/* checkbox - T&C */}
      <Row alignItems='center'>
        <Checkbox
          customClass={classNames(styles['checkbox--width'], checkboxColor)}
          iconColor={iconColor}
          nameIcon='Check'
          onClick={()=> {
            setAceptTerms(!acceptTerms)
            setStatusButton(!acceptTerms ? 'initial' : 'disabled')
          }}
          site={site}
          value={acceptTerms}
        />
        <Spacer.Vertical size={8} />
        <Text
          fontSize='md'
          fontWeight='regular'
          textAlign='center'
        >
          Acepto los&nbsp;
        </Text>
        <Link
          fontSize='md'
          fontWeight='regular'
          href={termsUrl}
          newTarget={true}
          underline='always'
        >
          términos y condiciones
        </Link>
      </Row>
    </Container>
  )
}