import React from 'react'
import {
  Column,
  Container,
  Spacer
} from '../../../../layout'
import { BigScreen } from '../../../../../helpers/mediaQueries'
import { MiddleFooterData, MiddleFooterEntriesData } from '..'
import { Text } from '../../../../atoms'
import { MiddleFooterContent } from './MiddleFooterContent'

interface MiddleFooterColumnsProps {
  item: MiddleFooterData;
}

export const MiddleFooterColumns = ({ item }: MiddleFooterColumnsProps): React.ReactElement => {
  const { label, entries } = item
  return(
    <Column 
      isWrap={true}
      justifyContent='center' 
      maxWidth='260px'
    >
      <Container alignItems='start' isWrap>
        <BigScreen>
          <Text fontWeight='medium'>{label}</Text>
        </BigScreen>
        <Spacer.Horizontal size={16} />
        { 
          entries?.length > 0 && entries.map((entry: MiddleFooterEntriesData , i:number) => {
            return(
              <React.Fragment key={`${item.contentType}__${i}`}>
                <MiddleFooterContent entry={entry}/>
              </React.Fragment>
            )
          })
        }
                    
      </Container>
    </Column>
  )

}