import {
  Column,
  Row,
  Spacer
} from '../../../../../../layout'
import { Icon, Link, Text } from '../../../../../../atoms'
import { MiddleFooterEntriesData } from '../../..'
import { getGlobalStyle } from '../../../../../../../helpers'
import { useMediaQuery } from 'react-responsive'

export const SubTitleWithIcon = ({ 
  description, 
  icon, 
  label, 
  link,
  subTitle 
}: MiddleFooterEntriesData): React.ReactElement => {
  const isMobile = useMediaQuery({ maxWidth: 1279 })
  const txtMessage = '%23AyudaUnimarc'
  const textWsp = isMobile ?
    `whatsapp://send?text=${txtMessage}&phone=${link}`:
    `https://web.whatsapp.com/send?text=${txtMessage}&phone=${link}`
  const target = isMobile ? false : true
  const linkFinal = icon === 'Phone' ? `tel:${link}` : textWsp
  return (
    <>
      <Text fontSize='md' fontWeight='semibold'>{label}</Text>
      <Spacer.Horizontal size={8} />
      <Row alignItems='center'>
        <Column customWidth='15'>
          <Icon
            color={getGlobalStyle('--color-icons-black')}
            name={icon}
            sizes='sm'
          />
        </Column>
        <Column>
          <Link
            color={getGlobalStyle('--color-icons-black')} 
            customWidth='100'
            fontSize='xl'
            fontWeight='semibold'
            href={linkFinal}
            newTarget={target}
            underline='none'
          >
            {subTitle}
          </Link>
        </Column>
      </Row>
      <Spacer.Horizontal size={2} />
      <Text color='gray' fontSize='md'>{description}</Text>
      <Spacer.Horizontal size={8} />
    </>
  )
}
